<template>
  <v-dialog :value="true" content-class="file-upload-dlg">
    <v-card>
      <v-card-text class="pa-0">
        <div
          class="dropzone-container"
          @dragover="dragover"
          @dragleave="dragleave"
          @drop="drop"
        >
          <input
            type="file"
            multiple
            name="file"
            id="fileInput"
            class="hidden-input"
            @change="onChange"
            ref="file"
            accept=".pdf,.jpg,.jpeg,.png"
          />

          <label for="fileInput" class="file-label">
            <div v-if="isDragging">Release to drop files here.</div>
            <div v-else>Drop files here or <u>click here</u> to upload.</div>
          </label>
        </div>
        <div class="uploaded-file-list">
          <div>업로드 파일: {{ files.length }} 건</div>
          <ul class="file-list-wrap">
            <li
              v-for="(item, i) in files"
              :key="`upload_file_${i}`"
              class="item"
            >
              <div class="file-name">{{ item.name }}</div>
              <v-btn class="white--text" color="btn_primary" x-small>
                <v-icon dark> mdi-minus </v-icon>
              </v-btn>
            </li>
          </ul>
        </div>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="$emit('close')"> 닫기 </v-btn>
        <v-btn color="primary" @click="addFiles"> 추가 </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data() {
    return {
      isDragging: false,
      files: [],
    };
  },
  methods: {
    addFiles() {
      this.$emit("addFiles", this.files);
    },
    onChange() {
      this.files.push(...this.$refs.file.files);

      //   this.$emit("updateFiles", this.files);
      console.log(this.files);
    },
    dragover(e) {
      e.preventDefault();
      this.isDragging = true;
    },
    dragleave() {
      this.isDragging = false;
    },
    drop(e) {
      e.preventDefault();
      this.$refs.file.files = e.dataTransfer.files;

      this.onChange();
      this.isDragging = false;
    },
  },
};
</script>

