<template>
  <section class="data-report-section">
    <v-row>
      <div style="padding: 10px">
        전체 파일 {{ allFileList.length }}개 중 {{ completeFileCnt() }}개 읽기
        완료
      </div>
    </v-row>
    <v-row>
      <v-simple-table>
        <thead>
          <tr>
            <th rowspan="2" style="width: 170px">이름</th>
            <!-- <th rowspan="2" style="width: 170px">이름2</th> -->
            <th v-for="month in monthList" :key="month" colspan="3">
              {{ month }}월
            </th>
          </tr>
          <tr>
            <template v-for="month in monthList">
              <th :key="`${month}_point`" style="width: 80px">지적활동</th>
              <th :key="`${month}_spot`" style="width: 80px">스팟활동</th>
              <th :key="`${month}_band`" style="width: 80px">밴드활동</th>
            </template>
          </tr>
        </thead>
        <tbody>
          <tr v-for="employee in Object.keys(allEmployeeList)" :key="employee">
            <td style="width: 170px">{{ employee }}</td>
            <!-- <td style="width: 170px">{{ parseName(employee) }}</td> -->
            <template v-for="month in monthList">
              <td :key="`${month}_value_point`">
                {{
                  pointActivity &&
                  pointActivity[month] &&
                  pointActivity[month][employee]
                    ? pointActivity[month][employee]
                    : 0
                }}
              </td>
              <td :key="`${month}_value_spot`">
                {{
                  spotActivity &&
                  spotActivity[month] &&
                  spotActivity[month][employee]
                    ? spotActivity[month][employee]
                    : 0
                }}
              </td>
              <td :key="`${month}_value_sum`">{{ sum(month, employee) }}</td>

              <!-- <template v-else-if="monthIdx == 1"></template>
                <template v-else-if="monthIdx == 2">
                  {{ pointActivity[month][employee] }}} -->
            </template>
          </tr>
        </tbody>
      </v-simple-table>
    </v-row>
  </section>
</template>

<script>
export default {
  name: "DataReport",
  props: {
    displayData: {
      type: Object,
    },
    monthList: {
      type: Array,
    },
    allEmployeeList: {
      type: Object,
    },
    pointActivity: {
      type: Object,
    },
    spotActivity: {
      type: Object,
    },
    allFileList: {
      tpye: Array,
    },
  },
  data() {
    return {
      headers: [],
    };
  },
  watch: {
    displayData: {
      handler() {
        this.initHeaders();
      },
      deep: true,
    },
    allEmployeeList: {
      deep: true,
      handler() {},
    },
  },
  created() {
    console.log(this.spotActivity);
  },
  methods: {
    sum(month, employee) {
      let pointCnt = 0;
      let spotCnt = 0;
      if (
        this.pointActivity &&
        this.pointActivity[month] &&
        this.pointActivity[month][employee]
      ) {
        pointCnt = this.pointActivity[month][employee] * 1;
      }

      if (
        this.spotActivity &&
        this.spotActivity[month] &&
        this.spotActivity[month][employee]
      ) {
        spotCnt = this.spotActivity[month][employee] * 1;
      }

      const sum = pointCnt + spotCnt;
      return sum;
    },
    completeFileCnt() {
      const complete = this.allFileList.filter((x) => x.load);
      return complete.length;
    },
    parseName(_name) {
      let name = _name;

      if (name.includes('"')) {
        console.log(name);
      }

      name = name.substr(0, 3);

      return name;
    },
    initHeaders() {
      console.log("@@@@initHeaders@@@");
    },
  },
};
</script>

<style scoped lang="scss">
.data-report-section {
  padding: 10px;
}
</style>
