<template>
  <v-app>
    <v-main>
      <v-container fluid>
        <v-layout class="">
          <v-row no-gutters>
            <!-- LEFT-->
            <v-col cols="3" class="list-box-group">
              <FileListBoxVue
                @addFiles="addFiles"
                @clearFiles="clearFiles"
                @removeFile="removeFile"
                :fileList="allFileList"
                :fileType="fileType"
                @changeType="changeType"
              ></FileListBoxVue>
              <EmployeeListBoxVue
                :allEmployeeList="allEmployeeList"
              ></EmployeeListBoxVue>
            </v-col>
            <!-- RIGHT -->
            <v-col cols="9">
              <DataReport
                v-if="allFileList.length > 0"
                :allFileList="allFileList"
                :displayData="displayData"
                :monthList="monthList"
                :allEmployeeList="allEmployeeList"
                :pointActivity="pointActivity"
                :spotActivity="spotActivity"
              >
              </DataReport>
            </v-col>
          </v-row>

          <div class="loading">
            <v-overlay v-model="loading">
              <v-progress-circular
                :size="50"
                color="primary"
                indeterminate
              ></v-progress-circular>
            </v-overlay>
          </div>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import EmployeeListBoxVue from "./components/EmployeeListBox.vue";
import FileListBoxVue from "./components/FileListBox.vue";
import DataReport from "./components/DataReport";
import * as xlsx from "xlsx";
export default {
  name: "viewMain",
  components: {
    FileListBoxVue,
    EmployeeListBoxVue,
    DataReport,
  },
  data() {
    return {
      fileType: "pointActivity",
      folders: [{ title: "a" }, { title: "b" }],
      fileList: [
        // {
        //   name: "가산데이터센터 현장(안전)_댓글내용_20240401.xlsx",
        // },
        // {
        //   name: "가산데이터센터 현장(안전)_댓글내용_20240402.xlsx",
        // },
      ],
      allFileList: [],
      loading: false,
      fileDatas: {},
      allEmployeeList: {},
      loadingFileReader: false,
      monthList: [], // 월 목록
      displayData: {}, //
      pointActivity: {}, // 지적활동
      spotActivity: {}, // 스팟활동

      loadingFlag: false,
    };
  },
  watch: {
    loadingFlag: {
      deep: true,
      handler() {
        const loadingCnt = this.allFileList.filter((x) => x.load);
        console.log("loadingCnt = ", loadingCnt);
        if (loadingCnt.length == this.allFileList.length) {
          this.loading = false;
        }
      },
    },
  },
  methods: {
    changeType(type) {
      this.fileType = type;
    },
    // 파일 삭제
    removeFile(file, index) {
      if (this.fileDatas[file.fileKey]) {
        delete this.fileDatas[file.fileKey];
      }
      this.allFileList.splice(index, 1);
      this.fileDatas = {};
      this.allEmployeeList = {};
      this.monthList = JSON.parse(JSON.stringify({}));
      this.displayData = JSON.parse(JSON.stringify({}));
      this.addFiles(this.allFileList);
    },
    // 파일 목록 초기화
    clearFiles() {
      this.allFileList = [];
      this.fileDatas = {};
      this.allEmployeeList = {};
    },
    // 파일 추가
    addFiles(files) {
      console.log("addFiles");
      //this.allFileList = files;
      files.map((file) => {
        const fileName = file.name;
        // key = 날짜 YYYYMMDD
        let splitFileName = fileName.split("_");
        splitFileName = splitFileName[splitFileName.length - 1];
        splitFileName = splitFileName.split(".");
        const fileKey = splitFileName[0]; // 파일 키

        // 월별 구분
        const monthKey = this.$moment(fileKey).format("MM");

        if (!this[this.fileType][monthKey]) {
          this[this.fileType] = {
            ...this[this.fileType],
            [monthKey]: {},
          };
        }

        // 파일 KEY
        if (!this[this.fileType][monthKey][fileKey]) {
          this[this.fileType] = {
            ...this[this.fileType],
            [monthKey]: {
              ...this[this.fileType][monthKey],
              [fileKey]: {},
            },
          };
        }

        if (!this.monthList.includes(monthKey)) {
          this.monthList.push(monthKey);
          this.monthList.sort();
        }
        // if (this.displayData[monthKey]) {
        //   if (!this.displayData[monthKey].monthList.includes(fileKey)) {
        //     this.displayData[monthKey].monthList.push(fileKey);
        //     this.displayData[monthKey].monthList.sort();
        //   }
        // } else {
        //   this.displayData[monthKey] = {
        //     monthList: [fileKey],
        //     datas: [],
        //   };
        // }

        // this.fileDatas[fileKey] = {
        //   employees: [],
        //   data: null,
        // };
        // const fileInfo = {
        //   fileKey: fileKey,
        //   name: fileName,
        //   monthKey: monthKey,
        // };

        const fileInfo = {
          fileKey: fileKey,
          fileType: this.fileType,
          fileName: fileName,
          monthKey: monthKey,
        };
        const findIdx = this.allFileList.findIndex(
          (x) => x.fileKey === fileKey && x.fileType == this.fileType
        );

        if (findIdx == -1) {
          this.allFileList.push({
            fileKey: fileKey,
            fileType: this.fileType,
            fileName: fileName,
            monthKey: monthKey,
            load: false,
          });
        } else {
          this.allFileList[findIdx] = {
            fileKey: fileKey,
            fileType: this.fileType,
            fileName: fileName,
            monthKey: monthKey,
            load: false,
          };
        }

        this.readFile(file, fileInfo);
        this.$forceUpdate();
      });
    },
    readFile(file, fileInfo) {
      const _this = this;
      this.loading = true;
      let reader = new FileReader();
      reader.onload = () => {
        const fileData = reader.result;
        const wb = xlsx.read(fileData, { type: "binary" });
        const toJson = xlsx.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
        _this
          .parseFileData(fileInfo, toJson)
          .then(() => {
            console.log("success");
            console.log("fileInfo = ", fileInfo);
            console.log(_this.allFileList);
            const find = _this.allFileList.findIndex(
              (x) =>
                x.fileKey == fileInfo.fileKey && x.fileType == fileInfo.fileType
            );
            if (find > -1) {
              _this.allFileList[find].load = true;
              _this.loadingFlag = !_this.loadingFlag;
            }
            this.$forceUpdate();
          })
          .catch(() => {
            _this.loading = false;
          });
      };
      reader.readAsBinaryString(file);
    },
    parseEmployeeName(_name) {
      if (!_name) {
        return "";
      }

      let name = _name;
      const delimiters = ["(", "/"];

      delimiters.map((delimiter) => {
        let splitIdx = name.indexOf(delimiter);
        if (splitIdx > -1) {
          name = name.substr(0, splitIdx);
          return;
        }
      });
      name = name.replaceAll('"', "");
      return name;
    },
    parseFileData(fileInfo, fileData) {
      return new Promise((resolve) => {
        for (let i = 0; i < 100; i++) {
          const data = fileData[i];
          if (!data) resolve();

          // 직원 이름
          const nameCell = data["이름"];
          let name = this.parseEmployeeName(nameCell);
          if (!name) {
            continue;
          }

          //name = name.substr(0, 3);

          if (!this.allEmployeeList[name]) {
            this.allEmployeeList = {
              ...this.allEmployeeList,
              [name]: {
                count: 1,
              },
            };
          } else {
            const count = this.allEmployeeList[name].count * 1;
            this.allEmployeeList = {
              ...this.allEmployeeList,
              [name]: {
                count: count + 1,
              },
            };
          }

          if (!this[this.fileType][fileInfo.monthKey][name]) {
            this[this.fileType][fileInfo.monthKey] = {
              ...this[this.fileType][fileInfo.monthKey],
              [name]: 1,
            };
          } else {
            const count = this[this.fileType][fileInfo.monthKey][name] * 1;
            this[this.fileType][fileInfo.monthKey] = {
              ...this[this.fileType][fileInfo.monthKey],
              [name]: count + 1,
            };
          }

          // if (this.allEmployeeList[name]) {
          //   this.allEmployeeList[name] = this.allEmployeeList[name] + 1;
          // } else {
          //   this.allEmployeeList = {
          //     ...this.allEmployeeList,
          //     [name]: {
          //       isCheck: true,
          //       count: 1,
          //     },
          //   };
          // }
        }

        // for (let i = 0; i < 100; i++) {
        //   const data = fileData[i];
        //   if (!data) resolve();
        //   const nameCell = data["이름"];
        // let name = this.parseEmployeeName(nameCell);

        // if (!name) {
        //   continue;
        // }

        // if (this.allEmployeeList[name]) {
        //   this.allEmployeeList[name] = this.allEmployeeList[name] + 1;
        // } else {
        //   this.allEmployeeList = {
        //     ...this.allEmployeeList,
        //     [name]: {
        //       isCheck: true,
        //       count: 1,
        //     },
        //   };
        // }

        //   let findIdx = -1;

        //   if (this.displayData[fileInfo.monthKey].datas.length > 0) {
        //     findIdx = this.displayData[fileInfo.monthKey].datas.findIndex(
        //       (x) => x.name === name
        //     );
        //   }

        //   if (findIdx == -1) {
        //     const addData = {
        //       name: name,
        //       [fileInfo.monthKey]: fileInfo.monthKey,
        //       count: 1,
        //     };
        //     this.displayData[fileInfo.monthKey].datas.push(addData);
        //   } else {
        //     const data = this.displayData[fileInfo.monthKey].datas[findIdx];
        //     console.log(data);
        //   }
        // }
        resolve();
      });
    },
  },
};
</script>
