        style="width: 100%; justify-content: space-between; align-items: center"
      >
        <div>
          <v-radio-group
            v-model="radioType"
            class="file-type-radio"
            hide-details
            @change="$emit('changeType', radioType)"
          >
            <v-radio label="지적활동" value="pointActivity"></v-radio>
            <v-radio label="스팟활동" value="spotActivity"></v-radio>
          </v-radio-group>
        </div>
        <div class="actions">
          <v-btn
            class="white--text"
            color="btn_primary"
            dark
            x-small
            @click="openFileUploadDlg = true"
          >
            업로드
          </v-btn>
          <v-btn
            class="ml-2 white--text"
            color="btn_primary"
            dark
            x-small
            @click="$emit('clearFiles')"
          >
            초기화
          </v-btn>
        </div>
      </div>
    </v-toolbar>
    <v-list class="file-list-wrap">
      <v-list-item-group active-class="pink--text">
        <template v-for="(item, index) in fileList">
          <v-list-item :key="item.title" v-if="item.fileKey">
            <div class="file-item">
              <div class="file-name">
                ({{
                  item.fileType == "pointActivity" ? "지적활동" : "스팟활동"
                }})
                {{ item.fileName }}
              </div>

              <v-btn
                class="btn-file-remove"
                @click="$emit('removeFile', item, index)"
              >
                <v-icon dark> mdi-minus </v-icon>
              </v-btn>
            </div>
            <v-divider v-if="index < items.length - 1" :key="index"></v-divider>
          </v-list-item>
        </template>
      </v-list-item-group>
    </v-list>
    <FileUploadDialogVue
      v-if="openFileUploadDlg"
      @close="closeDlg"
      @addFiles="addFiles"
    ></FileUploadDialogVue>
  </v-card>
</template>

<script>
import FileUploadDialogVue from "./FileUploadDialog.vue";

export default {
  name: "FileListBox",
  components: {
    FileUploadDialogVue,
  },
  props: {
    fileList: {
      type: Array,
    },
    fileType: {
      type: String,
    },
  },
  // ["fileList", "fileType"],
  data() {
    return {
      openFileUploadDlg: false,
      selected: [],
      radioType: "",
      items: [
        {
          action: "15 min",
          headline: "Brunch this weekend?",
          subtitle: `I'll be in your neighborhood doing errands this weekend. Do you want to hang out?`,
          title: "Ali Connors",
        },
        {
          action: "2 hr",
          headline: "Summer BBQ",
          subtitle: `Wish I could come, but I'm out of town this weekend.`,
          title: "me, Scrott, Jennifer",
        },
        {
          action: "6 hr",
          headline: "Oui oui",
          subtitle: "Do you have Paris recommendations? Have you ever been?",
          title: "Sandra Adams",
        },
      ],
    };
  },
  created() {
    this.radioType = this.fileType;
  },
  methods: {
    addFiles(files) {
      this.$emit("addFiles", files);

      this.closeDlg();
    },
    closeDlg() {
      this.openFileUploadDlg = false;
    },
  },
};
</script>
